import { gql } from '@apollo/client';

const GET_EVENTS = gql`
  query getEvents($filters: EventFilters!) {
    getEvents(filters: $filters) {
      id
      type
      lim
      limitDirection
      status
      executedAt
      orderOptions
      info
      observer {
        id
        quoteAsset
        baseAsset
      }
    }
  }
`;

export { GET_EVENTS };
