import styled from '@emotion/styled';
import React from 'react';
import { Controller } from 'react-hook-form';
import BaseInput from '../BaseInput';
import PropTypes from 'prop-types';
import { hexToRgbA } from 'theme/utils';

const Container = styled.div`
  display: flex;
  flex-direction: column;
  & > *:not(:last-child) {
    margin-bottom: 6px;
  }
`;

const Label = styled.label`
  font-size: 11px;
  font-weight: bold;
`;

const FakeInput = styled.div`
  display: flex;
  align-items: center;
  width: auto;
  background-color: ${({ theme }) => theme.white};
  border-radius: ${({ theme }) => theme.borderRadius};
  border: ${({ theme }) => theme.divider};
  -ms-touch-action: manipulation;
  touch-action: manipulation;
  &:has(input:focus) {
    box-shadow: ${({ theme }) =>
      `0px 0px 2px 3px ${hexToRgbA(theme.primary, 0.3)}`};
  }
  & > *:not(:last-child) {
    margin-left: 6px;
  }
`;

const ControlledInput = styled(BaseInput)`
  flex: 1;
  align-self: stretch;
  height: 100%;
  padding: 12px 8px;
  &[type='color'] {
    padding: 8px;
    height: 40px;
  }
`;

const Error = styled(Label)`
  color: ${({ theme }) => theme.errorDarken};
  font-weight: normal;
`;

const HelperText = styled(Label)`
  color: ${({ theme }) => theme.greyDarken};
  font-weight: normal;
`;

const Adornement = styled.span`
  color: ${({ theme }) => theme.greyDarken};
`;

const Input = ({
  formControl,
  autoFocus,
  rules,
  errors,
  label,
  name,
  defaultValue,
  type,
  required,
  disabled,
  helperText,
  placeholder,
  startIcon,
  startAdornement,
  endIcon,
  endAdornement,
  onChange,
  value,
}) => (
  <Container>
    <Label>
      {label}
      {required && '*'}
    </Label>
    <FakeInput>
      {startIcon && <span>{startIcon}</span>}
      {startAdornement && <Adornement>{startAdornement}</Adornement>}
      {formControl && (
        <Controller
          autoFocus={autoFocus}
          control={formControl}
          rules={rules}
          name={name}
          type={type}
          required={required}
          disabled={disabled}
          defaultValue={defaultValue}
          placeholder={placeholder}
          as={<ControlledInput />}
        />
      )}
      {!formControl && (
        <ControlledInput
          autoFocus={autoFocus}
          control={formControl}
          rules={rules}
          name={name}
          type={type}
          required={required}
          disabled={disabled}
          onChange={onChange}
          value={value}
          placeholder={placeholder}
        />
      )}
      {endAdornement && <span>{endAdornement}</span>}
      {endIcon && <Adornement>{endIcon}</Adornement>}
    </FakeInput>
    {helperText && <HelperText>{helperText}</HelperText>}
    {errors?.[name] && <Error>{errors[name].message}</Error>}
  </Container>
);

Input.defaultProps = {
  rules: {},
  defaultValue: undefined,
  autoFocus: false,
  name: undefined,
  value: undefined,
  onChange: undefined,
};

Input.propTypes = {
  name: PropTypes.string,
  rules: PropTypes.object,
  autoFocus: PropTypes.bool,
  defaultValue: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
};

export default Input;
