import { useEffect } from 'react';
import { useSnackbar } from '.';
import PropTypes from 'prop-types';

const RequestErrorHandler = ({ error }) => {
  const { error: errorToast } = useSnackbar();

  useEffect(() => {
    if (error) {
      errorToast(error.message || 'Unknow error', { closable: true });
    }
  }, [error]);

  return null;
};

RequestErrorHandler.defaultProps = {
  error: undefined,
};

RequestErrorHandler.propTypes = {
  error: PropTypes.shape({ message: PropTypes.string }),
};

export default RequestErrorHandler;
