import styled from '@emotion/styled';
import prevent from 'utils/prevent';
import PropTypes from 'prop-types';

const TabsContainer = styled.div`
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-content: stretch;
`;

const TabsNavContainer = styled.div`
  position: ${({ inModal }) => (inModal ? 'sticky' : 'relative')};
  top: ${({ inModal }) => (inModal ? '44px' : 'none')};
  width: 100%;
  display: flex;
  align-content: stretch;
  justify-content: center;
`;

const TabContentContainer = styled.div`
  width: 100;
  flex: 1;
`;

const Tab = styled.div`
  display: flex;
  justify-content: center;
  align-content: center;
  padding: 12px;
  border-bottom: ${({ theme, active }) =>
    active ? `2px solid ${theme.primary}` : `none`};
  cursor: pointer;
`;

const Tabs = ({ tabs, panes, value, onChange, inModal, paneProps }) => (
  <TabsContainer>
    <TabsNavContainer inModal={inModal}>
      {tabs.map(({ label, value: v }) => (
        <Tab onClick={prevent((e) => onChange(v))} active={v === value}>
          {label}
        </Tab>
      ))}
    </TabsNavContainer>
    <TabContentContainer>
      {panes.map(
        ({ value: v, Component }) => v === value && <Component {...paneProps} />
      )}
    </TabContentContainer>
  </TabsContainer>
);

Tabs.defaultProps = {
  value: undefined,
  onChange: undefined,
  inModal: false,
  paneProps: {},
};

Tabs.propTypes = {
  tabs: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string.isRequired,
      value: PropTypes.string.isRequired,
    }).isRequired
  ),
  panes: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string.isRequired,
      Component: PropTypes.func.isRequired,
    }).isRequired
  ),
  value: PropTypes.string,
  onChange: PropTypes.func,
  inModal: PropTypes.bool,
  paneProps: PropTypes.object,
};

export default Tabs;
