import styled from '@emotion/styled';
import Icon from 'components/Icon';
import Tooltip from 'components/Tooltip';
import dayjs from 'dayjs';
import React from 'react';
import useScreenType from 'theme/useScreenType';
import ucfirst from 'utils/ucfirst';

const OrderBlockContainer = styled.div`
  height: 100%;
  padding: 6px;
  border-radius: 3px;
  border: 1px solid rgba(0, 0, 0, 0.125);
`;

const OrderText = styled.strong`
  color: ${({ theme, default: _default }) =>
    _default ? theme.default : theme.errorDarken};
`;

const OrderBlock = ({ status, orderOptions, info }) => (
  <React.Fragment>
    <OrderBlockContainer>
      {status !== 'executed' && (
        <React.Fragment>
          [<OrderText default>{orderOptions.symbol}</OrderText>]{' '}
          <OrderText default>{orderOptions.side}</OrderText>,{' '}
          <OrderText>{orderOptions.type}</OrderText>
          {orderOptions.type === 'LIMIT' && (
            <OrderText>{`: ${orderOptions.price} (ba)`}</OrderText>
          )}
          , quantity:
          <OrderText> {orderOptions.quantity}</OrderText>
        </React.Fragment>
      )}
      {status === 'executed' && (
        <React.Fragment>
          [<OrderText default>{info.symbol}</OrderText>]{' '}
          <OrderText default>{info.side}</OrderText>,{' '}
          <OrderText>{info.type}</OrderText>
          {info.type === 'LIMIT' && (
            <OrderText>{`: ${info.price} (ba)`}</OrderText>
          )}
          , quantity:
          <OrderText> {info.quantity}</OrderText>,{' '}
          <a
            rel='noreferrer'
            target='_blank'
            href='https://www.binance.com/en/my/orders/exchange/openorder'
          >
            {info.orderId}
          </a>
        </React.Fragment>
      )}
    </OrderBlockContainer>
    {status === 'failed' && (
      <OrderBlockContainer>
        <OrderText>{info.error}</OrderText>
      </OrderBlockContainer>
    )}
  </React.Fragment>
);

const CardContainer = styled.div`
  padding: 12px;
  &:not(:last-child) {
    border-bottom: 1px solid rgba(0, 0, 0, 0.125);
  }
`;

const FlexCardContainer = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
`;

const FlexInfoContainer = styled.div`
  display: flex;
  align-items: center;
  & > *:not(:last-child) {
    margin-right: 12px;
  }
`;

const EventType = styled.div`
  padding: 6px 12px;
  border-radius: 3px;
  border: 2px solid;
  border-color: ${({ theme, isOrder }) =>
    isOrder ? theme.errorDarken : theme.successDarken};
  color: ${({ theme, isOrder }) =>
    isOrder ? theme.errorDarken : theme.successDarken};
`;

const EventCard = ({ event }) => {
  const { isUnderTablet } = useScreenType();
  return (
    <CardContainer>
      <FlexCardContainer>
        <FlexInfoContainer>
          <EventType isOrder={event.type === 'order'}>
            {ucfirst(event.type)}
          </EventType>
          <div>
            <div>
              <strong>{`${event.observer.baseAsset}/${event.observer.quoteAsset}`}</strong>
            </div>
            <div>
              <strong>{event.limitDirection}</strong> - limit:{' '}
              <strong>
                {event.lim} {event.observer.quoteAsset}
              </strong>
            </div>
          </div>
          {event.type === 'order' && !isUnderTablet && (
            <OrderBlock
              status={event.status}
              orderOptions={event.orderOptions}
              info={event.info}
            />
          )}
        </FlexInfoContainer>
        {event.status === 'pending' && (
          <Icon icon='processing' color='warningDarken' />
        )}
        {event.status === 'executed' && (
          <Tooltip
            title={`Executed at ${dayjs(event.executedAt).format(
              'DD/MM/YYYY HH:mm:ss'
            )}`}
          >
            <Icon icon='validCircle' color='successDarken' />
          </Tooltip>
        )}
        {event.status === 'failed' && (
          <Tooltip
            title={`Failed at ${dayjs(event.executedAt).format(
              'DD/MM/YYYY HH:mm:ss'
            )}`}
          >
            <Icon icon='failed' color='errorDarken' />
          </Tooltip>
        )}
      </FlexCardContainer>
    </CardContainer>
  );
};

export default EventCard;
