import styled from '@emotion/styled';
import React from 'react';
import PropTypes from 'prop-types';
import { hexToRgbA } from 'theme/utils';
import Icon from 'components/Icon';
import Loader from 'components/Loader/Loader';
import Tooltip from 'components/Tooltip';
import { useTheme } from '@emotion/react';

const BaseButton = styled.button`
  outline-color: ${({ theme, color }) => theme.getColor(color)};
  padding: 0;
  margin: 0;
  border: ${({ theme }) => theme.divider};
  border-radius: ${({ theme }) => theme.borderRadius};
  width: ${({ fullWidth }) => (fullWidth ? '100%' : 'auto')};
  cursor: pointer;
  padding: ${({ padding }) => padding};
  font-size: ${({ fontSize }) => fontSize};
`;

const ContainedButton = styled(BaseButton)`
  display: flex;
  align-items: center;
  justify-content: center;
  background: ${({ theme, color }) => theme.forceDefault(color)};
  color: ${({ theme, color }) => theme.getTextColor(color)};
  transition: ${({ theme }) => theme.transition};
  :hover:not(:disabled) {
    background-color: ${({ theme, color }) => theme.forceDarken(color)};
  }
  :disabled {
    opacity: 0.7;
    cursor: default;
  }
`;

const OutlinedButton = styled(BaseButton)`
  display: flex;
  align-items: center;
  justify-content: center;
  background: ${({ theme }) => theme.transparent};
  color: ${({ theme, color }) => theme.getColor(color)};
  border: ${({ theme, color, naked }) =>
    naked ? 'none' : `1px solid ${theme.getColor(color)}`};
  transition: ${({ theme }) => theme.transition};
  :hover:not(:disabled) {
    border-color: ${({ theme, color }) => theme.forceDarken(color)};
    color: ${({ theme, color }) => theme.forceDarken(color)};
    background: ${({ theme, color }) =>
      hexToRgbA(theme.forceLighten(color), 0.125)};
  }
  :disabled {
    opacity: 0.7;
    cursor: default;
  }
`;

const IconContainer = styled.span`
  margin-right: 6px;
`;

const computeSize = (size) => {
  switch (size) {
    case 'sm':
      return {
        fontSize: '12px',
        padding: '4px 8px',
      };
    default:
    case 'md':
      return {
        fontSize: '13.33px',
        padding: '6px 12px',
      };
    case 'lg':
      return {
        fontSize: '14.2px',
        padding: '8px 16px',
      };
  }
};

const Button = ({
  variant,
  color,
  loading,
  disabled,
  children,
  type,
  icon,
  size,
  tooltip,
  ...others
}) => {
  const { fontSize, padding } = computeSize(size);
  const theme = useTheme();

  let ButtonComponent = null;

  switch (variant) {
    default:
    case 'outlined':
    case 'naked':
      ButtonComponent = OutlinedButton;
      break;
    case 'contained':
      ButtonComponent = ContainedButton;
      break;
  }

  if (tooltip)
    ButtonComponent = ({ children, ...props }) => (
      <Tooltip title={tooltip}>
        <ButtonComponent {...props}>{children}</ButtonComponent>
      </Tooltip>
    );

  return (
    <ButtonComponent
      type={type}
      {...others}
      color={color}
      fontSize={fontSize}
      padding={padding}
      disabled={loading || disabled}
      naked={variant === 'naked'}
    >
      {icon && !loading && (
        <IconContainer>
          <Icon
            icon={icon}
            size='xs'
            color={variant !== 'contained' ? color : theme.colors.text(color)}
          />
        </IconContainer>
      )}
      {loading && (
        <IconContainer>
          <Loader
            size='xs'
            color={variant !== 'contained' ? color : theme.colors.text(color)}
          />
        </IconContainer>
      )}
      {children}
    </ButtonComponent>
  );
};

Button.defaultProps = {
  color: 'default',
  type: 'button',
  variant: 'outlined',
  size: 'sm',
  tooltip: undefined,
};

Button.propTypes = {
  type: PropTypes.string,
  size: PropTypes.string,
  tooltip: PropTypes.string,
  variant: PropTypes.oneOf(['outlined', 'contained']),
  color: PropTypes.oneOf(['primary', 'secondary', 'default']),
};

export default Button;
