import { gql } from '@apollo/client';

const CREATE_OBSERVER = gql`
  mutation createObserver($input: CreateObserverInput!) {
    createObserver(input: $input) {
      id
      name
      baseAsset
      quoteAsset
    }
  }
`;

const UPDATE_OBSERVER = gql`
  mutation updateObserver($id: ID!, $input: UpdateObserverInput!) {
    updateObserver(id: $id, input: $input) {
      id
      name
      baseAsset
      quoteAsset
    }
  }
`;

const DELETE_OBSERVER = gql`
  mutation deleteObserver($id: ID!) {
    deleteObserver(id: $id)
  }
`;

export { CREATE_OBSERVER, UPDATE_OBSERVER, DELETE_OBSERVER };
