import Modal from 'components/Modal';
import React from 'react';
import TradingViewWidget, { Themes } from 'react-tradingview-widget';

const CandlesModal = ({ observer, onClose }) => {
  const SYMBOL = `${observer.baseAsset}${observer.quoteAsset}`;
  return (
    <Modal
      size='lg'
      title={observer.name}
      hasCloseButton
      open
      onClose={onClose}
    >
      <TradingViewWidget
        symbol={`BINANCE:${SYMBOL}`}
        theme={Themes.LIGHT}
        locale='fr'
        width='100%'
        height={800}
        show
        hide_side_toolbar={false}
        details
        allow_symbol_change={false}
      />
    </Modal>
  );
};

export default CandlesModal;
