import Divider from 'components/Divider';
import { Input, Select } from 'components/Inputs';
import { ModalContent } from 'components/Modal';
import Tabs from 'components/Tabs';
import { useProducts } from 'contexts/Products';
import React from 'react';

const CommonFormFields = ({ formControl, errors }) => {
  const { products } = useProducts();
  return (
    <React.Fragment>
      <Select
        required
        formControl={formControl}
        label='Symbol'
        options={products.map(({ symbol, baseAsset, quoteAsset }) => ({
          label: `${symbol} [${baseAsset}/${quoteAsset}]`,
          value: `${baseAsset}${quoteAsset}`,
        }))}
        name='orderOptions[symbol]'
        placeholder='No symbol selected'
        errors={errors}
      />
      <Select
        formControl={formControl}
        label='Side'
        options={[
          {
            label: 'BUY',
            value: 'BUY',
          },
          {
            label: 'SELL',
            value: 'SELL',
          },
        ]}
        name='orderOptions[side]'
        defaultValue='BUY'
        errors={errors}
      />
      <Input
        name='orderOptions[quantity]'
        label='Quantity'
        required
        rules={{
          validate: (v) => /[+-]?([0-9]*[.])?[0-9]+/.test(v) || 'Error',
        }}
        formControl={formControl}
        errors={errors}
      />
    </React.Fragment>
  );
};

const LimitTab = ({ formControl, errors }) => (
  <React.Fragment>
    <CommonFormFields formControl={formControl} errors={errors} />
    <Input
      name='orderOptions[price]'
      label='Price'
      rules={{
        validate: (v) => /[+-]?([0-9]*[.])?[0-9]+/.test(v) || 'Error',
      }}
      required
      formControl={formControl}
      errors={errors}
    />
  </React.Fragment>
);
const MarketTab = ({ formControl, errors }) => (
  <React.Fragment>
    <CommonFormFields formControl={formControl} errors={errors} />
  </React.Fragment>
);

const tabs = [
  { label: 'Limit', value: 'limit' },
  { label: 'Market', value: 'market' },
];

const panes = [
  { Component: LimitTab, value: 'limit' },
  { Component: MarketTab, value: 'market' },
];

const OrderForm = ({ formControl, errors, orderType, setOrderType }) => {
  return (
    <ModalContent>
      <Input
        required
        name='lim'
        label='Limit to reach'
        formControl={formControl}
        errors={errors}
      />
      <Select
        options={[
          { label: 'UP', value: 'UP' },
          { label: 'DOWN', value: 'DOWN' },
        ]}
        defaultValue='UP'
        name='limitDirection'
        label='Direction (cross by)'
        formControl={formControl}
        errors={errors}
      />
      <Divider />
      <Tabs
        value={orderType}
        onChange={setOrderType}
        tabs={tabs}
        panes={panes}
        paneProps={{ formControl, errors }}
      />
    </ModalContent>
  );
};

export default OrderForm;
