import styled from '@emotion/styled';
import React from 'react';
import { Controller } from 'react-hook-form';
import PropTypes from 'prop-types';
import { hexToRgbA } from 'theme/utils';

const Container = styled.div`
  display: flex;
  flex-direction: column;
  & > *:not(:last-child) {
    margin-bottom: 6px;
  }
`;

const Label = styled.label`
  font-size: 11px;
  font-weight: bold;
`;

const FakeInput = styled.div`
  display: flex;
  align-items: center;
  width: 100%;
  background-color: ${(theme) => theme.white};
  border-radius: ${({ theme }) => theme.borderRadius};
  border: ${({ theme }) => theme.divider};
  -ms-touch-action: manipulation;
  touch-action: manipulation;
  & select:focus {
    box-shadow: ${({ theme }) =>
      `0px 0px 2px 3px ${hexToRgbA(theme.primary, 0.3)}`};
  }
  & > *:not(:last-child) {
    margin-left: 6px;
  }
`;

const ControlledInput = styled.select`
  outline: none;
  height: 100%;
  width: 100%;
  margin-right: 6px;
  padding: 12px 8px;
  border: none;
  background-color: ${(theme) => theme.white};
`;

const Error = styled(Label)`
  color: ${({ theme }) => theme.errorDarken};
  font-weight: normal;
`;

const HelperText = styled(Label)`
  color: ${({ theme }) => theme.greyDarken};
  font-weight: normal;
`;

const Select = ({
  formControl,
  autoFocus,
  rules,
  errors,
  label,
  name,
  defaultValue,
  type,
  required,
  disabled,
  helperText,
  placeholder,
  options,
}) => (
  <Container>
    <Label>
      {label}
      {label && required && '*'}
    </Label>
    <FakeInput>
      <Controller
        autoFocus={autoFocus}
        control={formControl}
        rules={rules}
        name={name}
        type={type}
        required={required}
        disabled={disabled}
        defaultValue={defaultValue}
        placeholder={placeholder}
        as={<ControlledInput />}
      >
        {placeholder && <option value={null}>{placeholder}</option>}
        {options.map((opt) => (
          <option value={opt.value}>{opt.label}</option>
        ))}
      </Controller>
    </FakeInput>
    {helperText && <HelperText>{helperText}</HelperText>}
    {errors?.[name] && <Error>{errors[name].message}</Error>}
  </Container>
);

Select.defaultProps = {
  rules: {},
  defaultValue: undefined,
  autoFocus: false,
};

Select.propTypes = {
  name: PropTypes.string.isRequired,
  rules: PropTypes.object,
  autoFocus: PropTypes.bool,
  defaultValue: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
};

export default Select;
