import styled from '@emotion/styled';
import React from 'react';
import BaseInput from '../BaseInput';
import PropTypes from 'prop-types';

const Container = styled.div`
  margin-bottom: ${({ noMargin }) => (noMargin ? 0 : '12px')};
  display: flex;
  align-items: center;
`;

const Label = styled.label`
  font-size: 11px;
  font-weight: bold;
  white-space: nowrap;
  flex: 0;
  display: flex;
  align-items: center;
  & > *:not(:first-child) {
    margin-left: 6px;
  }
`;

const FakeInput = styled.div`
  outline-color: ${({ theme }) => theme.primary};
`;

const ControlledInput = styled(BaseInput)`
  flex: 1;
  align-self: stretch;
  height: 100%;
  padding: 12px 8px;
  &[type='color'] {
    padding: 8px;
    height: 40px;
  }
`;

const Error = styled(Label)`
  color: ${({ theme }) => theme.errorDarken};
  font-weight: normal;
`;

const HelperText = styled(Label)`
  color: ${({ theme }) => theme.greyDarken};
  font-weight: normal;
`;

const Checkbox = ({
  formRef,
  rules,
  errors,
  label,
  name,
  defaultChecked,
  required,
  disabled,
  helperText,
  onChange,
  checked,
  noMargin,
}) => (
  <Container noMargin>
    <Label>
      <FakeInput>
        <input
          ref={formRef}
          name={name}
          type='checkbox'
          required={required}
          disabled={disabled}
          defaultChecked={defaultChecked}
          onChange={onChange}
          checked={checked}
        />
      </FakeInput>
      <span>
        {label}
        {required && '*'}
      </span>
    </Label>
    {helperText && <HelperText>{helperText}</HelperText>}
    {errors?.[name] && <Error>{errors[name].message}</Error>}
  </Container>
);

Checkbox.defaultProps = {
  defaultChecked: false,
  onChange: undefined,
  checked: undefined,
};

Checkbox.propTypes = {
  name: PropTypes.string,
  label: PropTypes.string,
  rules: PropTypes.object,
  checked: PropTypes.bool,
  onChange: PropTypes.func,
  defaultChecked: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
};

export default Checkbox;
