import { useMutation } from '@apollo/client';
import Button from 'components/Buttons/Button';
import { Input, Select } from 'components/Inputs';
import Form from 'components/Inputs/Form';
import Modal, { ModalActions, ModalContent } from 'components/Modal';
import { useProducts } from 'contexts/Products';
import { useSnackbar } from 'contexts/Snackbar';
import React from 'react';
import { useForm } from 'react-hook-form';
import { CREATE_OBSERVER, UPDATE_OBSERVER, DELETE_OBSERVER } from './mutations';

const EditModal = ({ initialObserver, onClose }) => {
  const { control, handleSubmit, errors } = useForm({
    defaultValues: { initialObserver },
  });
  const creating = !initialObserver;
  const title = creating
    ? 'New observer'
    : `Editing ${initialObserver.name} ...`;
  const { error } = useSnackbar();

  const [save, { loading }] = useMutation(
    creating ? CREATE_OBSERVER : UPDATE_OBSERVER,
    {
      onError: ({ message }) => error(message),
      onCompleted: () => onClose && onClose(),
      refetchQueries: ['getObservers'],
    }
  );

  const [deleteObserver, { loading: deleting }] = useMutation(DELETE_OBSERVER, {
    refetchQueries: ['getObservers'],
    onCompleted: () => onClose && onClose(),
    onError: ({ message }) => error(message),
  });

  const { products } = useProducts();

  return (
    <Modal notCancellable title={title} hasCloseButton open onClose={onClose}>
      <Form
        onSubmit={handleSubmit(async (values) => {
          const { name, symbol } = values;
          const product = products.find((p) => p.symbol === symbol);

          if (!product) {
            error('Choose product before continue.');
            return;
          }

          const { baseAsset, quoteAsset } = product;

          const input = {
            name,
            baseAsset,
            quoteAsset,
          };

          const variables = creating
            ? { input }
            : { id: initialObserver.id, input };

          save({
            variables,
          });
        })}
      >
        <ModalContent>
          <Input
            autoFocus
            required
            formControl={control}
            label='Observer name'
            placeholder='E.g Observer BTC/USDT'
            name='name'
            defaultValue={initialObserver?.name}
            errors={errors}
          />
          <Select
            required
            errors={errors}
            formControl={control}
            label='Pair to observer'
            options={products.map(({ symbol, baseAsset, quoteAsset }) => ({
              label: `${symbol} [${baseAsset}/${quoteAsset}]`,
              value: `${baseAsset}${quoteAsset}`,
            }))}
            name='symbol'
            placeholder='No symbol selected'
            defaultValue={`${initialObserver?.baseAsset}${initialObserver?.quoteAsset}`}
            disabled={!creating}
          />
        </ModalContent>

        <ModalActions>
          {!creating && (
            <Button
              onClick={() =>
                window.confirm('This action cannot be undo. Are you sure?') &&
                deleteObserver({ variables: { id: initialObserver.id } })
              }
              disabled={loading || deleting}
              variant='contained'
              color='error'
            >
              Delete
            </Button>
          )}
          <Button
            disabled={loading || deleting}
            type='submit'
            variant='contained'
            color='primary'
          >
            Save
          </Button>
        </ModalActions>
      </Form>
    </Modal>
  );
};

export default EditModal;
