import styled from '@emotion/styled';
import Icon from 'components/Icon';
import { Input } from 'components/Inputs';
import Checkbox from 'components/Inputs/Checkbox';
import Container from 'components/Layouts/Container';
import React from 'react';

const FiltersContainer = styled.div`
  margin-bottom: 12px;
  display: flex;
  justify-content: space-between;
  align-items: center;
`;

const Filters = ({ eventFilters, onChangeEventFilters }) => {
  return (
    <Container>
      <FiltersContainer>
        <Checkbox
          label='Pending only'
          checked={eventFilters.pendingOnly}
          onChange={(e) =>
            onChangeEventFilters('pendingOnly', !eventFilters.pendingOnly)
          }
        />
        <Input
          value={eventFilters.search}
          placeholder='Search'
          startIcon={<Icon icon='search' size='xs' />}
          onChange={(e) => onChangeEventFilters('search', e.target.value)}
        />
      </FiltersContainer>
    </Container>
  );
};

export default Filters;
