import { useLazyQuery } from '@apollo/client';
import styled from '@emotion/styled';
import NoData from 'business/Alerts/List/NoData';
import Container from 'components/Layouts/Container';
import RequestLoader from 'components/Loader/RequestLoader';
import RequestErrorHandler from 'contexts/Snackbar/RequestErrorHandler';
import React, { useEffect } from 'react';
import EventCard from './EventCard';
import { GET_EVENTS } from './queries';

const EventsContainer = styled.div`
  border-radius: 3px;
  border: 1px solid rgba(0, 0, 0, 0.125);
`;

const List = ({ eventFilters }) => {
  const [getEvents, { data, error, loading }] = useLazyQuery(GET_EVENTS);
  useEffect(() => {
    getEvents({
      variables: { filters: eventFilters },
    });
  }, [getEvents, eventFilters]);

  if (loading) return <RequestLoader />;
  if (error) return <RequestErrorHandler error={error} />;

  if (data?.getEvents.length === 0)
    return (
      <Container>
        <NoData />
      </Container>
    );

  return (
    <Container>
      <EventsContainer>
        {data?.getEvents.map((event) => (
          <EventCard event={event} />
        ))}
      </EventsContainer>
    </Container>
  );
};

export default List;
