import { useMutation } from '@apollo/client';
import Button from 'components/Buttons/Button';
import Modal, { ModalActions } from 'components/Modal';
import Tabs from 'components/Tabs';
import { useSnackbar } from 'contexts/Snackbar';
import React, { useState } from 'react';
import { useForm } from 'react-hook-form';
import AlertForm from './AlertForm';
import { CREATE_ALERT } from './mutations';
import OrderForm from './OrderForm';

const tabs = [
  {
    label: 'Alert',
    value: 'alert',
  },
  {
    label: 'Order',
    value: 'order',
  },
];

const panes = [
  {
    Component: AlertForm,
    value: 'alert',
  },
  {
    Component: OrderForm,
    value: 'order',
  },
];

const AlertEditor = ({ observer, onClose }) => {
  const [value, setValue] = useState('alert');
  const { control, handleSubmit, errors } = useForm();
  const [orderType, setOrderType] = useState('limit');
  const { error } = useSnackbar();

  const [save, { loading: creating }] = useMutation(CREATE_ALERT, {
    onError: ({ message }) => error(message),
    onCompleted: onClose,
    refetchQueries: [
      'getEvents',
      value === 'alert' ? 'getPendingAlerts' : 'getPendingOrder',
      'getObservers',
    ],
  });

  return (
    <Modal
      open
      size='sm'
      title={`Add alert on ${observer.name}`}
      hasCloseButton
      onClose={onClose}
    >
      <form
        onSubmit={handleSubmit(async (values) => {
          const orderOptions =
            value === 'alert'
              ? undefined
              : { ...values.orderOptions, type: orderType };

          const input = {
            observerId: observer.id,
            ...values,
            type: value,
            orderOptions,
          };
          save({ variables: { input } });
        })}
      >
        <Tabs
          value={value}
          onChange={setValue}
          tabs={tabs}
          panes={panes}
          inModal
          paneProps={{ formControl: control, errors, orderType, setOrderType }}
        />
        <ModalActions>
          <Button
            disabled={creating}
            color='primary'
            variant='contained'
            type='submit'
          >
            Save
          </Button>
        </ModalActions>
      </form>
    </Modal>
  );
};

export default AlertEditor;
