import styled from '@emotion/styled';
import Button from 'components/Buttons/Button';
import IconButton from 'components/Buttons/IconButton';
import Icon from 'components/Icon';
import React, { useEffect, useState } from 'react';
import CardActions from './CardActions';

const Container = styled.div`
  position: relative;
  background: ${({ theme }) => theme.white};
  border-radius: ${({ theme }) => theme.borderRadius};
  box-shadow: 0px 0px 7px 0px rgba(0, 0, 0, 0.125);
  padding: 16px;
  min-height: 100%;
  min-width: 250px;
  transition: all 0.25s;
  @media ${({ theme }) => theme.isUpperTablet} {
    &:not(.add):hover {
      transform: translateY(-10px);
    }
  }
  &:hover .observer-card-action-container {
    display: block;
  }
`;

const Flexbox = styled.div`
  display: flex;
  justify-content: ${({ justify }) => justify || 'center'};
  align-items: ${({ align }) => align || 'center'};
  flex-direction: ${({ dir }) => dir || 'row'};
`;

const ObserverName = styled.div`
  font-weight: bold;
`;

const Asset = styled.span`
  color: ${({ theme }) => theme.getColor('grey')};
`;

const ArrowDir = styled.div`
  margin-left: 8px;
  transform: ${({ color }) =>
    color === 'successDarken'
      ? 'rotate(-45deg)'
      : color === 'errorDarken'
      ? 'rotate(45deg)'
      : 'none'};
  display: ${({ color }) => !color && 'hidden'};
  transition: all 0.25s ease;
`;

const Observation = styled.div`
  display: flex;
  align-items: center;
  font-weight: bold;
  font-size: 16px;
  color: ${({ theme, color }) => theme.getColor(color)};
`;

const ActionsContainer = styled.div`
  position: absolute;
  top: 10px;
  right: 10px;
  display: none;
  @media ${({ theme }) => theme.isUnderTablet} {
    display: block;
  }
`;

const ObserverCard = ({
  observer,
  onOpenEditModal,
  onOpenCandlesModal,
  onShowAlerts,
  onShowOrders,
  onAddAlert,
}) => {
  const [lastData, setLastData] = useState();
  const [data, setData] = useState();

  useEffect(() => {
    const symbol = `${observer.baseAsset}${observer.quoteAsset}`.toLowerCase();
    const URL = `wss://stream.binance.com:9443/ws/${symbol}@ticker`;
    let socket = new WebSocket(URL);
    socket.onmessage = (event) => {
      const eventData = JSON.parse(event.data);
      setData((state) => {
        setLastData(state);
        return eventData;
      });
    };

    return () => socket && socket.close();
  }, [observer.baseAsset, observer.quoteAsset]);

  const color =
    (Number(data?.['c']) > Number(lastData?.['c']) && 'successDarken') ||
    (Number(data?.['c']) < Number(lastData?.['c']) && 'errorDarken');

  return (
    <Container>
      <Flexbox align='flex-start' justify='space-between'>
        <ObserverName>{observer.name}</ObserverName>
        <ActionsContainer className='observer-card-action-container'>
          <IconButton
            onClick={(e) => onAddAlert(e, observer)}
            icon='add'
            size='xs'
          />
          <IconButton
            onClick={(e) => onOpenCandlesModal(e, observer)}
            icon='candleStick'
            size='xs'
          />
          <IconButton
            onClick={(e) => onOpenEditModal(e, observer)}
            icon='edit'
            size='xs'
          />
        </ActionsContainer>
      </Flexbox>
      <Asset>{observer.baseAsset}</Asset> / <Asset>{observer.quoteAsset}</Asset>
      <Observation color={color}>
        {!data && <Icon icon='loader' size='xs' />}
        {data && (
          <React.Fragment>
            {data['c']}{' '}
            <ArrowDir color={color}>
              <Icon icon='arrowSkin' color={color} size='xs' />
            </ArrowDir>
          </React.Fragment>
        )}
      </Observation>
      <CardActions
        observer={observer}
        onShowAlerts={onShowAlerts}
        onShowOrders={onShowOrders}
      />
    </Container>
  );
};

const Text = styled.p`
  text-align: center;
`;

const AddObserverCard = ({ onAddObserver }) => {
  return (
    <Container className='add'>
      <Flexbox dir='column'>
        <Text>Add new observer at any time</Text>
        <Button onClick={onAddObserver} color='primary'>
          Add observer
        </Button>
      </Flexbox>
    </Container>
  );
};

export { AddObserverCard };
export default ObserverCard;
