import styled from '@emotion/styled';
import React, { createRef, useCallback, useEffect } from 'react';
import { Controller } from 'react-hook-form';
import PropTypes from 'prop-types';
import { hexToRgbA } from 'theme/utils';
import BaseTextArea from '../BaseTextArea';

const Container = styled.div`
  display: flex;
  flex-direction: column;
  & > *:not(:last-child) {
    margin-bottom: 6px;
  }
`;

const Label = styled.label`
  font-size: 11px;
  font-weight: bold;
`;

const FakeInput = styled.div`
  display: flex;
  align-items: center;
  background-color: ${(theme) => theme.white};
  border-radius: ${({ theme }) => theme.borderRadius};
  border: ${({ theme }) => theme.divider};
  -ms-touch-action: manipulation;
  touch-action: manipulation;
  & textarea:focus {
    box-shadow: ${({ theme }) =>
      `0px 0px 2px 3px ${hexToRgbA(theme.primary, 0.3)}`};
  }
  & > *:not(:last-child) {
    margin-left: 6px;
  }
`;

const ControlledInput = styled(BaseTextArea)`
  flex: 1;
  align-self: stretch;
  height: 100%;
  padding: 12px 8px;
  &[type='color'] {
    padding: 8px;
    height: 40px;
  }
`;

const Error = styled(Label)`
  color: ${({ theme }) => theme.errorDarken};
  font-weight: normal;
`;

const HelperText = styled(Label)`
  color: ${({ theme }) => theme.greyDarken};
  font-weight: normal;
`;

const TextArea = ({
  formControl,
  autoFocus,
  rules,
  errors,
  label,
  name,
  defaultValue,
  type,
  required,
  disabled,
  rows,
  helperText,
  placeholder,
}) => {
  const textAreaRef = createRef();

  const resize = useCallback(() => {
    textAreaRef.current.style.height = 'auto';
    textAreaRef.current.style.height = textAreaRef.current.scrollHeight + 'px';
  }, [textAreaRef]);

  useEffect(() => {
    if (textAreaRef.current) {
      resize();
    }
  }, [resize, textAreaRef]);

  useEffect(() => {
    if (textAreaRef.current) {
      textAreaRef.current.addEventListener('keydown', function (e) {
        if (e.key === 'Tab') {
          e.preventDefault();
          let start = this.selectionStart;
          let end = this.selectionEnd;

          this.value =
            this.value.substring(0, start) + '\t' + this.value.substring(end);

          this.selectionStart = this.selectionEnd = start + 1;
        }
      });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [textAreaRef.current?.value]);

  const handleChange = (e, cb) => {
    resize();
    cb(e);
  };

  return (
    <Container>
      <Label>
        {label}
        {required && '*'}
      </Label>
      <FakeInput>
        <Controller
          autoFocus={autoFocus}
          control={formControl}
          rules={rules}
          name={name}
          type={type}
          required={required}
          disabled={disabled}
          defaultValue={defaultValue}
          placeholder={placeholder}
          rows={rows}
          render={({ onChange, value }) => (
            <ControlledInput
              ref={textAreaRef}
              onChange={(e) => handleChange(e, onChange)}
              value={value}
            />
          )}
        />
      </FakeInput>
      {helperText && <HelperText>{helperText}</HelperText>}
      {errors?.[name] && <Error>{errors[name].message}</Error>}
    </Container>
  );
};

TextArea.defaultProps = {
  rules: {},
  defaultValue: undefined,
  autoFocus: false,
  rows: 5,
};

TextArea.propTypes = {
  name: PropTypes.string.isRequired,
  rules: PropTypes.object,
  autoFocus: PropTypes.bool,
  rows: PropTypes.number,
  defaultValue: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
};

export default TextArea;
