import { useQuery } from '@apollo/client';
import EventCard from 'business/Events/List/EventCard';
import RequestLoader from 'components/Loader/RequestLoader';
import RequestErrorHandler from 'contexts/Snackbar/RequestErrorHandler';
import React from 'react';

import NoData from './NoData';
import { GET_EXECUTED_ALERTS, GET_PENDING_ALERTS } from './queries';

const List = ({ observer, type }) => {
  const pending = type === 'pending';
  const QUERY = pending ? GET_PENDING_ALERTS : GET_EXECUTED_ALERTS;
  const resultKey = pending ? 'getPendingAlerts' : 'getExecutedAlerts';

  const { data, error, loading } = useQuery(QUERY, {
    variables: { id: observer.id },
  });

  if (error) return <RequestErrorHandler error={error} />;
  if (loading) return <RequestLoader />;

  if (data?.[resultKey].length === 0) return <NoData />;
  return data?.[resultKey].map((alert) => <EventCard event={alert} />);
};

const PendingList = ({ observer }) => (
  <List observer={observer} type='pending' />
);

const ExecutedList = ({ observer }) => (
  <List observer={observer} type='executed' />
);

export { ExecutedList, PendingList };
