import { useMediaQuery } from 'react-responsive';

import mediaQueries from './mediaQueries';

const useScreenType = () => {
  const isMobile = useMediaQuery({
    query: mediaQueries.isMobile,
  });
  const isTablet = useMediaQuery({
    query: mediaQueries.isTablet,
  });
  const isDesktop = useMediaQuery({
    query: mediaQueries.isDesktop,
  });
  const isUnderTablet = useMediaQuery({
    query: mediaQueries.isUnderTablet,
  });
  const isUpperTablet = useMediaQuery({
    query: mediaQueries.isUpperTablet,
  });
  const isUnderDesktop = useMediaQuery({
    query: mediaQueries.isUnderDesktop,
  });

  return {
    isMobile,
    isTablet,
    isDesktop,
    isUnderTablet,
    isUpperTablet,
    isUnderDesktop,
  };
};

export default useScreenType;
