import { gql } from '@apollo/client';

const GET_PENDING_ALERTS = gql`
  query getPendingAlerts($id: ID!) {
    getPendingAlerts(id: $id) {
      id
      lim
      limitDirection
      status
      type
      executedAt
      orderOptions
      info
      observer {
        id
        baseAsset
        quoteAsset
      }
    }
  }
`;
