import { useTheme } from '@emotion/react';
import styled from '@emotion/styled';
import IconButton from 'components/Buttons/IconButton';
import PropTypes from 'prop-types';
import React from 'react';
import BaseModal from 'react-modal';
import { hexToRgbA } from 'theme/utils';

BaseModal.setAppElement('#root');

const computeMaxWidth = ({ size, theme }) => {
  switch (size) {
    default:
    case 'sm':
      return theme.modal.smallWidth;
    case 'md':
      return theme.modal.mediumWidth;
    case 'lg':
      return theme.modal.largeWidth;
  }
};

const computeMinWidth = ({ size, theme }) => {
  switch (size) {
    default:
    case 'sm':
      return theme.modal.smallWidth;
    case 'md':
      return 'none';

    case 'lg':
      return 'none';
  }
};

const getStyle = ({ theme, size, zIndex }) => ({
  overlay: {
    zIndex,
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    background: hexToRgbA(theme.white, 0.85),
    outline: 'none',
  },
  content: {
    position: 'relative',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    background: theme.background1,
    border: theme.divider,
    width: '100%',
    maxHeight: '100%',
    maxWidth: computeMaxWidth({ theme, size }),
    minWidth: computeMinWidth({ theme, size }),
    outline: 'none',
    boxShadow: '0px 0px 2px 0px rgba(0, 0, 0, 0.1)',
    padding: 0,
    overflow: 'auto',
  },
});

const Header = styled.div`
  position: sticky;
  top: 0;
  width: 100%;
  padding: 8px 12px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: ${({ theme }) => theme.divider};
  background: ${({ theme }) => theme.background1};
`;

const ModalContent = styled.div`
  padding: 12px;
  overflow-y: auto;
`;

const ModalActions = styled.div`
  position: sticky;
  bottom: 0;
  padding: 6px;
  display: flex;
  border-top: ${({ theme }) => theme.divider};
  background: ${({ theme }) => theme.background1};
  justify-content: flex-end;
  & > *:not(:last-of-type) {
    margin-right: 12px;
  }
`;

/**
 * Modal
 * @component
 *
 */
function Modal({
  title,
  open,
  onAfterOpen,
  onClose,
  hasCloseButton,
  size,
  children,
  notCancellable,
  zIndex,
}) {
  const theme = useTheme();

  return (
    <BaseModal
      style={getStyle({ theme, size, zIndex })}
      isOpen={open}
      onAfterOpen={onAfterOpen}
      onRequestClose={onClose}
      shouldCloseOnEsc={!notCancellable}
      shouldCloseOnOverlayClick={!notCancellable}
    >
      <Header>
        <span>{title}</span>
        {hasCloseButton && (
          <IconButton onClick={onClose} size='xs' icon='close' />
        )}
      </Header>
      {children}
    </BaseModal>
  );
}

Modal.defaultProps = {
  title: null,
  hasCloseButton: false,
  size: 'sm',
  children: null,
  disableBodySpacing: false,
  notCancellable: false,
  zIndex: 100000,
};
Modal.propTypes = {
  title: PropTypes.string,
  open: PropTypes.bool.isRequired,
  onClose: PropTypes.func.isRequired,
  hasCloseButton: PropTypes.bool,
  size: PropTypes.oneOf(['sm', 'md']),
  notCancellable: PropTypes.bool,
  zIndex: PropTypes.number,
  children: PropTypes.node,
};

export { ModalContent, ModalActions };
export default Modal;
