import { useQuery } from '@apollo/client';
import React, { useState } from 'react';
import { GET_OBSERVERS } from './queries';
import styled from '@emotion/styled';
import { AddObserverCard } from './ObserverCard';
import ObserverCard from './ObserverCard';
import RequestErrorHandler from 'contexts/Snackbar/RequestErrorHandler';
import Container from 'components/Layouts/Container';
import CandlesModal from '../CandlesModal';
import EditModal from '../EditModal';
import AlertsList from 'business/Alerts/List';
import AlertEditor from 'business/Alerts/Editor';

const CustomContainer = styled.div`
  margin-top: -100px;
`;

const ObserversContainer = styled.div`
  max-width: 100vw;
  padding: 16px;
  overflow-x: auto;
  &::-webkit-scrollbar {
    display: none;
  }
  display: flex;
  justify-content: flex-start;
  @media ${({ theme }) => theme.isUpperTablet} {
    justify-content: center;
  }
  & > *:not(:last-of-type) {
    margin-right: 16px;
  }
`;

const Text = styled.div`
  color: ${({ theme }) => theme.white};
`;

const List = () => {
  const { data, error } = useQuery(GET_OBSERVERS);

  const [editModalOpen, setEditModalOpen] = useState(() => false);
  const [candlesModalOpen, setCandlesModalOpen] = useState(() => false);
  const [showAlerts, setShowAlerts] = useState(false);
  const [showOrders, setShowOrders] = useState(false);
  const [addAlert, setAddAlert] = useState(false);
  const [initialObserver, setInitialObserver] = useState(() => false);

  const onOpenEditModal = (e, observer) => {
    e.preventDefault();
    e.stopPropagation();
    setInitialObserver(observer);
    setEditModalOpen(true);
  };
  const onCloseEditModal = () => {
    setInitialObserver(null);
    setEditModalOpen(false);
  };

  const onOpenCandlesModal = (e, observer) => {
    setInitialObserver(observer);
    setCandlesModalOpen(true);
  };
  const onCloseCandlesModal = () => {
    setInitialObserver(null);
    setCandlesModalOpen(false);
  };

  const onShowAlerts = (e, observer) => {
    setInitialObserver(observer);
    setShowAlerts(true);
  };
  const onCloseAlerts = (e) => {
    setInitialObserver(null);
    setShowAlerts(false);
  };

  const onShowOrders = (e, observer) => {
    setInitialObserver(observer);
    setShowOrders(true);
  };
  const onCloseOrders = (e) => {
    setInitialObserver(null);
    setShowOrders(false);
  };

  const onAddAlert = (e, observer) => {
    setInitialObserver(observer);
    setAddAlert(true);
  };
  const onCloseAddAlert = (e) => {
    setInitialObserver(null);
    setAddAlert(false);
  };

  if (error) return <RequestErrorHandler error={error} />;
  if (!data) return null;

  return (
    <CustomContainer>
      <Container>
        <Text>
          You are currently observing {data ? data.getObservers.length : '...'}{' '}
          symbol(s)
        </Text>
      </Container>
      <ObserversContainer>
        <AddObserverCard onAddObserver={onOpenEditModal} />
        {data &&
          data.getObservers.map((obs) => (
            <ObserverCard
              observer={obs}
              onOpenEditModal={onOpenEditModal}
              onOpenCandlesModal={onOpenCandlesModal}
              onShowAlerts={onShowAlerts}
              onShowOrders={onShowOrders}
              onAddAlert={onAddAlert}
            />
          ))}
      </ObserversContainer>
      {editModalOpen && (
        <EditModal
          initialObserver={initialObserver}
          onClose={onCloseEditModal}
        />
      )}
      {candlesModalOpen && (
        <CandlesModal
          observer={initialObserver}
          onClose={onCloseCandlesModal}
        />
      )}
      {showAlerts && initialObserver && (
        <AlertsList observer={initialObserver} onClose={onCloseAlerts} />
      )}
      {showOrders && initialObserver && (
        <AlertsList
          isOrder
          observer={initialObserver}
          onClose={onCloseOrders}
        />
      )}
      {addAlert && initialObserver && (
        <AlertEditor observer={initialObserver} onClose={onCloseAddAlert} />
      )}
    </CustomContainer>
  );
};

export default List;
